import axios from "axios";

export const pizzaAPI = {
    getPizza: (sortBy, category) => {


        // return axios.get(`https://kalinskiy.github.io/pizza/db.json/pizzas?${
        //     category !== null ? `category=${category}` : ''}&_sort=${sortBy.type}&_order=${sortBy.order}`).then(res => res.data)
        return axios.get(`https://kalinskiy.github.io/pizza/db.json/?${
            category !== null ? `category=${category}` : ''}&_sort=${sortBy.type}&_order=${sortBy.order}`).then(res => res.data)
    }
}